<template>
    <div class="home">
        <el-carousel :interval="5000" :height="'550px'" arrow="always" indicator-position="outside">
            <el-carousel-item v-for="item in 4" :key="item">
                <div class="carousel"
                     :style="{backgroundImage: 'url(http://img1.imgtn.bdimg.com/it/u=3428204051,23413526&fm=200&gp=0.jpg)'}"></div>
            </el-carousel-item>
        </el-carousel>
        <div class="img-list">
            <div v-for="item in images"
                 :key="item.url"
                 @click="$router.push('/picture/' + item.name)"
                 :style="{backgroundImage: item.url}"
                 class="item">
                <div class="dec">
                    <h1>标题</h1>
                </div>
            </div>
            <br style="clear:both;"/>
        </div>
    </div>
</template>

<script>
    const url = (name) => `url(./static/img/main/${name}.jpg)`;
    export default {
        data() {
            return {
                images: [1, 2, 3, 4, 5, 6, 7, 8, 9].map(name => ({
                    url: url(name),
                    name: name
                }))
            };
        },
    }
</script>

<style lang="less">
    .home {
        .carousel {
            width: 100%;
            height: 100%;
            background-repeat: no-repeat;
            background-size: cover;
            cursor: pointer;
        }

        .img-list {
            margin: 0 auto;
            width: 1392px;

            .item {
                position: relative;
                float: left;
                margin: 8px;
                width: 448px;
                height: 289px;
                background-repeat: no-repeat;
                background-size: cover;
                cursor: pointer;

                .dec {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    text-align: left;
                    background: rgba(0, 0, 0, 0.7);
                    color: white;
                    display: none;
                }

                &:hover .dec{
                    display: block;
                }
            }
            .item:nth-child(9n - 8) {
                width: 911px;
                height: 594px;
            }
            .item:nth-child(9n - 4) {
                float: right;
                margin-left: 0;
                width: 911px;
                height: 594px;
            }
        }
    }
</style>



