import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/main';
import Picture from '@/components/Picture';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Main',
            component: Main
        }, {
            path: '/Picture/:pictureName',
            name: 'Picture',
            component: Picture
        }
    ]
})



