<template>
    <div class="picture">
        <div :style="{backgroundImage: images[curImgIndex].url}" class="picture__main">
            <h2>{{images[curImgIndex].desc}}</h2>
        </div>
        <div class="picture__bottom">
            <div v-for="(item, index) in images"
                 :key="item.url"
                 @click="curImgIndex = index"
                 :style="{backgroundImage: item.url}"
                 class="picture__img-item"
                 :class="{'picture__img-item-chosen': index === curImgIndex}">
            </div>
            <br style="clear:both;"/>
            <h4>描述描述描述描述描述描述描述描述描述描述描述描述描述</h4>
        </div>
    </div>
</template>

<script>
    const url = (name) => `url(./static/img/main/${name}.jpg)`;
    export default {
        data() {
            return {
                images: [1, 2, 3, 4, 5, 6, 7, 8, 9].map(name => ({
                    url: url(name),
                    name: name,
                    desc: '标题' + name
                })),
                curImgIndex: 0
            };
        },
    }
</script>

<style lang="less" scoped>
    .picture {
        margin: 0 auto;
        margin-top: 8px;
        width: 911px;
        text-align: left;

        .picture__main {
            width: 100%;
            height: 594px;
            background-repeat: no-repeat;
            background-size: cover;
        }

        .picture__bottom {
            padding: 64px;
            background: #000;
            color: #fff;

            .picture__img-item {
                float: left;
                margin-right: 8px;
                width: 50px;
                height: 50px;
                border-radius: 2px;
                border: 3px solid transparent;
                background-repeat: no-repeat;
                background-size: cover;
                cursor: pointer;

                &:hover {
                    border-color: #989898;
                }
            }

            .picture__img-item-chosen {
                border-color: #989898;
            }
        }


    }
</style>



